* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

.container {
  background-color: darkslategray;
  margin-top: 1.225rem;
  border-radius: 0.625rem;
}

.container button {
  color: black;
  outline: none;
  width: 25rem;
}
.container .row .col {
  display: flex;
  flex-direction: column;
  /* row-gap: 1.25rem; */
  padding: 1.25rem;
}

.container .row .col .inp {
  font-size: 2rem;
  text-align: right;
}

.container .row .col .buttons div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.625rem;
}

.container .row .col .buttons div .btn {
  width: 3.875rem;
  text-align: center;
  font-size: 1.175rem;
}


.container .row .col input {
  height: 3.25rem;
}

@media (min-width: 400px) {
  .container {
    width: 23rem;
  }
}

@media (min-width: 700px) {
  .container {
    width: 25rem;
  }
}

@media (max-width: 300px) {
  .container .row .col .buttons div .btn {
    width: 2.575rem;
    text-align: center;
    font-size: 1rem;
  }
  .container .row .col .inp {
    font-size: 1.125rem;
  }
  .container .row .col input {
    height: 2.55rem;
  }
  .container button {
    color: black;
    outline: none;
  }
}
